<template>
  <div id="app">
    <div class="content">
      <div class="content-header">
        <div class="padding">
          <h1>Welcome</h1>
        </div>
      </div>
      <div class="content-main">
        <div class="padding">
          <!-- <p>
            Choose the button below to set the color of the selected range to
            green.
          </p>
          <br /> -->
          <h3>Actions</h3>
          <button @click="onTextBufferImport">Import from Duolingo buffer</button><br />
          <button @click="onLoadImageFront">Load images (Front)</button>
          <button @click="onLoadImageBack">Load images (Back)</button>
          <button @click="onFindImageByText">Find image by selected cell</button><br />
          <button @click="onLoadSoundFront">Load sounds (Front)</button>
          <button @click="onLoadSoundBack">Load sounds (Back)</button><br />
          <button @click="onCSVExport">Export to CSV</button>
        </div>

        <div v-if="images.length">
          <h3>Images</h3>
          <div v-for="(image, index) in images" :key="index">
            <img class="responsive-img" :src="image" @click="handleImageClick($event)">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { EventHandler } from '../code/EventHandler';
import * as Utils from '../code/Utils';

export default {
  name: 'Root',
  data: function () {
    return {
      eventHandler: new EventHandler(),
      images: []
    };
  },

  mounted() {
    this.eventHandler.init();
    this.images = [];
  },

  methods: {
    onTextBufferImport() { this.eventHandler.onTextBufferImport(); },
    onLoadImageFront() { this.eventHandler.onLoadImageFront(); },
    onLoadImageBack() { this.eventHandler.onLoadImageBack(); },
    onLoadSoundFront() { this.eventHandler.onLoadSoundFront(); },
    onLoadSoundBack() { this.eventHandler.onLoadSoundBack(); },
    onCSVExport() { this.eventHandler.onCSVExport(); },

    async onFindImageByText() {
      this.images = [];

      await Excel.run(async (context) => {
        const activeCell = context.workbook.getActiveCell();
        activeCell.load("values");
        await context.sync();
        if (!activeCell.values) {
          return;
        }
        const allImages = await this.eventHandler.getImageByText(activeCell.values[0][0]);

        this.images.push(...allImages);
      });
    },

    async handleImageClick(event) {
      const src = event.target.src;
      await Excel.run(async (context) => {
        const sheet = context.workbook.worksheets.getActiveWorksheet();
        const currentCell = context.workbook.getActiveCell()
        currentCell.load("rowIndex");
        await context.sync();

        sheet.getCell(currentCell.rowIndex, Utils.IMAGE_COLUMN).values = [[src]];
        await context.sync();

        this.images = [];
      });
    }
  }
}
</script>



