/* global console, Excel, Office, responsiveVoice */
import * as Vue from "vue";
import root from "./components/Root.vue";
import * as Utils from './code/Utils';

// The initialize function must be run each time a new page is loaded
Office.onReady(async (info) => {
  if (info.host === Office.HostType.Excel) {
    new Vue({
      el: "#app",
      render: (h) => h(root, {}),
    });

    try {
      await Excel.run(async (context) => {
        const sheets = "Duo,toPolish".split(",");
        for (const sheetName of sheets) {
          const sheet = context.workbook.worksheets.getItem(sheetName);
          if (sheet) {
            sheet.onSelectionChanged.add(onSelectionChange);
          }
        }
        await context.sync();
      });
    } catch (error) {
      console.error(error);
    }
  }
});

async function onSelectionChange() {
  await Excel.run(async (context) => {
    // Get the current selected range
    const sheet = context.workbook.worksheets.getActiveWorksheet();

    sheet.load(["name"]);
    await context.sync();

    if (sheet.name === "Duo" || sheet.name === "toPolish") {
      checkDuoSelection(context, sheet, sheet.name === "Duo");
    }
  });
}

async function checkDuoSelection(context: Excel.RequestContext, sheet: Excel.Worksheet, isFront: boolean) {
  const range = context.workbook.getActiveCell();

  // Load the address of the selected range
  range.load(["rowIndex", "columnIndex", "values"]);
  await context.sync();

  const text = range.values[0][0];
  switch (range.columnIndex) {
    case Utils.IMAGE_COLUMN:
      if (Utils.isValidURL(text)) {
        sheet.getRange(`${Utils.columnIndexToLetter(Utils.IMAGE_COLUMN)}1`).formulas = [[`=IMAGE("${text}")`]]; // Set the formula to display the image
        await context.sync(); // Don't forget to sync after setting the formula
      }
      break;
    case Utils.SOUND_COLUMN:
      if (text) {
        const specificCell = sheet.getCell(range.rowIndex, isFront ? Utils.FRONT_COLUMN : Utils.BACK_COLUMN);
        specificCell.load("values");
        await context.sync();
        const originalText = Utils.removeHTMLTags(specificCell.values[0][0]);

        const sheetOption = context.workbook.worksheets.getItem("Opt");
        const rangeVoice = sheetOption.getRange("LANG_VOICE");
        rangeVoice.load("values");
        await context.sync();

        const language = rangeVoice.values[0][0];
        responsiveVoice.speak(originalText, language);
      }
      break;
  }
}
